import Nav from "./Nav"; import ReCAPTCHA from "react-google-recaptcha";

import React, { Component } from "react";
import axios from "axios";
import * as $ from "jquery";

import {
  MDBInput,
  MDBDropdown,
  MDBDropdownToggle,
  MDBDropdownMenu,
  MDBDropdownItem
} from "mdbreact";

class App extends Component {


  constructor(props) {
    super(props);
    this.onCaptcha = this.onCaptcha.bind(this)
    this.state = {
      uniqueid: "",
      name: "",
      school: "",
      designation: "",
      mobile: "",
      email: "",
      location: "",
      one: "Delhi",
      two: "Mumbai",
      three: "Kolkata",
      four: "Chennai",
      five: "Ahmedabad",
      six: "Bengaluru",
      seven: "Pune",
      captcha: false
    };
  }

  onCaptcha(value) {
    axios.post('https://perplexed.tech/captcha/verify', { 'g-recaptcha-response': value })
      .then(res => {
        console.log(res.data['success']);

        if (res.data['success'])
          this.setState({ captcha: true })
      })
  }

  handleSubmit = event => {
    event.preventDefault();

    const user = {
      uniqueid: this.state.uniqueid,
      name: this.state.name,
      school: this.state.school,
      designation: this.state.designation,
      mobile: this.state.mobile,
      email: this.state.email,
      location: this.state.location
    };
    if (this.state.location) {
      if (this.state.captcha) {
        $("#subBut").toggleClass("submit");
        $("#loader").toggleClass("submit");
        axios.post(`https://perplexed.tech/atl`, user).then(res => {
          if (res.data["success"]) {
            $("#foo").toggleClass("submit");
            $("#success").toggleClass("submit");
          }
        }).catch(err => console.log(err)
        );
      }
      else {
        window.alert('Please complete captcha.')
      }
    } else {
      window.alert("Please select location.");
      $("#naya")
        .removeClass("btn-primary")
        .toggleClass("btn-danger");
    }
  };

  render() {
    return (
      <React.Fragment>
        <Nav />

        <div className="App">
          <div className="container">
            <p className="title">Atal Tinkering Labs</p>
            <div className="dabba">
              <div id="success" className="submit successFulDabba">
                <div className="successFul">Submitted Successfully</div>
              </div>
              <form id="foo" onSubmit={this.handleSubmit}>
                <MDBInput
                  label="Unique ID"
                  type="text"
                  name="uniqueid"
                  id="uniqueid"
                  value={this.state.uniqueid}
                  onChange={e => this.setState({ uniqueid: e.target.value })}
                  required
                />{" "}
                <MDBInput
                  label="Name"
                  type="text"
                  name="name"
                  id="name"
                  value={this.state.name}
                  onChange={e => this.setState({ name: e.target.value })}
                  required
                />
                <MDBInput
                  label="School"
                  type="text"
                  name="school"
                  id="school"
                  value={this.state.school}
                  onChange={e => this.setState({ school: e.target.value })}
                  required
                />{" "}
                <MDBInput
                  label="Designation"
                  type="text"
                  name="designation"
                  id="designation"
                  value={this.state.designation}
                  onChange={e => this.setState({ designation: e.target.value })}
                  required
                />{" "}
                <MDBInput
                  label="Contact Number"
                  type="number"
                  name="mobile"
                  id="mobile"
                  value={this.state.mobile}
                  onChange={e => this.setState({ mobile: e.target.value })}
                  required
                />{" "}
                <MDBInput
                  label="E-Mail"
                  type="email"
                  name="email"
                  id="email"
                  value={this.state.email}
                  onChange={e => this.setState({ email: e.target.value })}
                  required
                />{" "}
                <MDBDropdown dropright className="dropdown">
                  <MDBDropdownToggle
                    caret
                    color="primary"
                    id="naya"
                    onClick={e =>
                      $("#naya")
                        .removeClass("btn-danger")
                        .addClass("btn-primary")
                    }
                  >
                    Select Event Location
                  </MDBDropdownToggle>
                  <MDBDropdownMenu basic>
                    <MDBDropdownItem
                      onClick={e => {
                        this.setState({ location: this.state.one });
                      }}
                    >
                      {" "}
                      {this.state.one}
                    </MDBDropdownItem>
                    <MDBDropdownItem
                      onClick={e => {
                        this.setState({ location: this.state.two });
                      }}
                    >
                      {" "}
                      {this.state.two}
                    </MDBDropdownItem>
                    <MDBDropdownItem
                      onClick={e => {
                        this.setState({ location: this.state.three });
                      }}
                    >
                      {" "}
                      {this.state.three}
                    </MDBDropdownItem>
                    <MDBDropdownItem
                      onClick={e => {
                        this.setState({ location: this.state.four });
                      }}
                    >
                      {" "}
                      {this.state.four}
                    </MDBDropdownItem>
                    <MDBDropdownItem
                      onClick={e => {
                        this.setState({ location: this.state.five });
                      }}
                    >
                      {" "}
                      {this.state.five}
                    </MDBDropdownItem>
                    <MDBDropdownItem
                      onClick={e => {
                        this.setState({ location: this.state.six });
                      }}
                    >
                      {" "}
                      {this.state.six}
                    </MDBDropdownItem>
                    <MDBDropdownItem
                      onClick={e => {
                        this.setState({ location: this.state.seven });
                      }}
                    >
                      {" "}
                      {this.state.seven}
                    </MDBDropdownItem>
                  </MDBDropdownMenu>{" "}
                  {this.state.location || "Select Venue"}
                </MDBDropdown>
                <ReCAPTCHA
                  className='captcha'
                  sitekey="6LdazKkUAAAAAEtWME0kErnhhwZwvVqZEZs__UtO"
                  onChange={this.onCaptcha}
                />
                <div className="buttonDabba">
                  <button type="submit" className=" btn button">
                    {" "}
                    <span id="subBut">Submit</span>
                    <div className="submit spinner-border" id="loader" role="status">
                      <span className="sr-only" />
                    </div>
                  </button>
                </div>
              </form>
            </div>{" "}
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default App;
